'use strict';

var name = "angular",
    version = 2,
    map = "func";

function tool() {
   console.log('tooling');
}

var framework = {
   name, //match the names of the variables
   version,
   tool,
   track() {
   // you can declare a function inside an object
      console.log('track');
   },
   [map]: function() {
      // same as framework['func'] = function(){}
      console.log('func');
   }
};


// Expression bodies
var odds = evens.map(v => v + 1);
var nums = evens.map((v, i) => v + i);

// Statement bodies
nums.forEach(v => {
   if (v % 5 === 0)
      fives.push(v);
});

// Lexical this
var bob = {
   _name: "Bob",
   _friends: [],
   printFriends() {
      this._friends.forEach(f =>
         console.log(this._name + " knows " + f));
   }
};


framework.tool();
framework.track();
